<template>
<div>
<van-row>
  <van-nav-bar
            title=""
            left-text=""
            left-arrow
            right-text=""
            @click-left="abcdback"
        />

</van-nav-bar>
</van-row>

  <br/>
  <br/>
<van-row name="">
<h4></h4>
  <van-col span=""><van-checkbox v-model="checked"></van-checkbox></van-col>
  <van-col span=""><van-checkbox v-model="checked"></van-checkbox></van-col>
</van-row>
<van-row>
  <h4></h4><br/>
  <van-col span=""><van-checkbox v-model="checked"></van-checkbox></van-col>
  <van-col span=""><van-checkbox v-model="checked"></van-checkbox></van-col>
  <van-col span=""><van-checkbox v-model="checked"></van-checkbox></van-col>
  <van-col span=""><van-checkbox v-model="checked"></van-checkbox></van-col>
</van-row>
<van-row><h4></h4></van-row>
<van-dropdown-menu>
  <van-dropdown-item v-model="value1" :options="option1" />
</van-dropdown-menu>
<van-row>
<h4></h4>
</van-row>
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />

<van-cell-group>
  <van-field
    v-model="message"
    label=""
    type="textarea"
    placeholder=""
    rows="3"
    autosize
  />
</van-cell-group>

<van-tabbar
  v-model="active"
  active-color="#07c160"
  inactive-color="#000"
>
  <van-button round type="danger" size="large"></van-button>
</van-tabbar>

<!--  -->
<van-pull-refresh v-model="isLoading" @refresh="onRefresh">
  <p> {{ count }}</p>
</van-pull-refresh>

<router-view></router-view>
</div>
</template>

<script>
export default {
  name:"Addshop",
   data() {
    return {
      checked: true,
      radio: '2',
// 
      value1: 1,
      option1: [
        { text: '', value: 0 },
        { text: '', value: 1 },
        { text: '', value: 2 }
      ],
      // 
      count: 0,
      isLoading: false,

      fileList: [
        { url: 'https://img.yzcdn.cn/vant/cat.jpeg' }
      ]
    }
  },

methods: {
  //
    onClickLeft() {
      Toast('');
    },
    abcdback(){
      this.$router.go(-1)
    }
},
// 
onRefresh() {
      setTimeout(() => {
        this.$toast('');
        this.isLoading = false;
        this.count++;
      }, 500);
  },
// 
    beforeRead(file) {
      if (file.type !== 'image/jpeg') {
        Toast(' jpg ');
        return false;
      }

      return true;
    },

    //  Promise
    asyncBeforeRead(file) {
      return new Promise((resolve, reject) => {
        if (file.type !== 'image/jpeg') {
          Toast(' jpg ');
          reject();
        } else {
          resolve();
        }
      });
    }
}
</script>

<style>
/* */
