<template>
  <div>
   
    <van-nav-bar
            :title="title"
            left-text=""
            left-arrow
            right-text=""
            @click-left="onClickLeft"
        />
        <van-tabs v-model="active">
            <van-tab title="">


                <van-card
                  @click="godetail"
                  price="2.00"
                  desc=""  
                  title=""
                  thumb="https://img.yzcdn.cn/vant/t-thirt.jpg"
                >
                </van-card>

            </van-tab>
            <van-tab title="">

                <div class="likefather">

                  <div @click="godetail(i-1+2)" class="like left" v-for="(item,i) in list" :key="i">
                    <img :src="item.path" alt="">
                    <p>{{item.goodsName}}</p>
                    <span>{{item.money}}</span>
                  </div>
                  
                </div>

            </van-tab>
            
        </van-tabs>
  </div>
</template>
<script>
import axios from 'axios';
export default {
  name:"Collect",
  data() {
    return {
      title:"",
      active: 2,
      collectimg:"https://img.yzcdn.cn/vant/cat.jpeg",
      collectname:"",
      collectprice:"99.99",
      list:[],
    }
  },
  methods: {
    onClickLeft(){
      this.$router.go(-1)
    },
    godetail(id){
      //console.log("")
      this.$router.push("/xqy/id=" + id);
    }
  },
  mounted(){
    var _this = this
    //
    axios({
      method: 'post',
      url:"http://106.12.14.214:8889/luxury/goods/listShow",
      params:{
        flag:1
      }
    }).then((data)=>{
      //console.log(data.data)
      _this.list = data.data
    })
  }
}
</script>
<style scoped>
.left{float:left}
.likefather{
  overflow: hidden;
  height: auto;
}
.like{
  width: 48%;
  height: 300px;
  border: 1px solid #ccc;
  margin: 2px;
}
.like img{
  width: 98%;
  height: 220px;
}
</style>



