<template>
    <div>
        <van-nav-bar
            :title="title"
            left-text=""
            left-arrow
            @click-left="onClickLeft"
        />
        <van-address-list
            v-model="chosenAddressId"
            :list="list"
            @add="onAdd"
            @edit="onEdit"
        />
       <div id="adress">
           <van-cell-group>
            <van-field
                id="adressname"
                v-model="lianxiren"
                required
                clearable
                label=""
                right-icon="question-o"
                placeholder=""
                @click-right-icon="$toast('question')"
            />
            <van-field
                id="adresstel"
                v-model="tel"
                type="tel"
                label=""
                placeholder=""
                required
            />
            <van-field
            id="adressadress"
                v-model="adress"
                type="tel"
                label=""
                placeholder=""
                required
            />
            <van-button @click="tijiao" type="info"></van-button>
             
            </van-cell-group>
       </div>


    </div>
</template>
<script>
import axios from 'axios';
export default {
    name:"Address",
    data(){
        return{
            title:"",
            chosenAddressId: '1',
            username:"",
            lianxiren:"",
            tel:"",
            adress:"",
            
            list: [
                {
                id: 0,
                name: '',
                tel: '13000000000',
                address: ' 138  7  501 '
                }
            ]
        }
    },
    methods:{
        onClickLeft() {
            this.$router.go(-1)
        },
        onAdd() {
            adress.style.display="initial"
        },
        tijiao(){
            var index = this.list.length
            this.list.push({
                id:index,
                name:adressname.value,
                tel:adresstel.value,
                address:adressadress.value
            })
            adress.style.display="none"
        },
        onEdit() {
            //
            for(let i = 0;i<this.list.length;i++){
                //console.log(this.list[i].id)
                this.list.slice(this.list[i].id,1)
            }
        }
    },
    mounted() {
        //axios
    }
}
</script>
<style scoped>
#adress{
    display: none
}
</style>



